
.contact {
    color:white;
    background-color: yellow;
    background-image:url("../../assets/images/roseImage.jpg");
    background-size: cover;
    text-align: center;
    background-position: center;
    font-size: xx-large;
}
.container {
    height: 20vh;
    display: flex;
    flex-direction: column;
    object-fit: contain;
    align-items: right;
    justify-content: end;
    color: black;
    bottom: 0;
    left: 0;
    
    :hover { 
        cursor: pointer;
        translate: 0px 500px 0px;
        transition: all 0.5s ease;
        -webkit-transition: all 0.5 ease;
     
    }
}

img   {
    display: flex;
    flex-direction: column;
    height: 20vh;
    position: fixed;
    bottom: 0;
    background-size: cover;
    background-position: center;
    z-index: 4;

}
